@import url('https://fonts.googleapis.com/css2?family=Dongle&family=Lato&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Neonderthaw&display=swap');

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    width: 10px;
   
  }
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  ::-webkit-scrollbar-thumb {
    background: #666;
    
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Dongle', sans-serif;
  
}
li {
    list-style: none;
}

a {
    text-decoration: none;
}
.header{
   width: 100%;
    height: 100px;
    z-index: 10000;
    transition: 50ms;
}


.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.2rem 1.5rem;
    margin-right: 36%;
    

    
}
.navbar__logo img {
    width: 140px;
    height: 110px;
    
}
.hamburger {
    display: none;
    
}

.bar {
   display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
   
}
.nav-menu {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    margin-left: 65%;
    margin-top: -2%;
}

.nav-item {
  
    margin-left: 4.5rem;
    z-index: 10;
}

.nav-link{
    font-size: 1.9rem;
    
    font-weight: 600;

    color: #101010;
    transition: 250ms;
}

.nav-link:hover{
    color: gray;
}
.nav-link:hover .nav-item {
    border-bottom: 2px solid black;
}



/*=========Home Section=========*/



  .home__welcome {
    width: 45%;
    height: auto;
    margin-left: 12%;
    margin-top: 5%;
    background-attachment: fixed;
}
.welcome__title {
    color: black;
    font-size: 55px;
    font-family: 'Dongle', sans-serif;
    animation-name: welcome__title-anim;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1s;
}
.welcome__title a {
    color: black !important;
}
.welcome__title a:hover {
    color: black !important;
}
@keyframes welcome__title-anim {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
.welcome__subtitle {
    font-size: 65px;
    font-weight: 600;
    font-family: 'Neonderthaw', cursive;
    color: black;
    margin-top: -11%;
    animation-name: welcome__title-anim;
    animation-delay: 2s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1s;
}
.welcome__title a {
    text-decoration: none;
    color: white;
    /*font-family: 'Neonderthaw', cursive;
    font-size: 40px;
    text-shadow: 1px 3px 2px rgba(255,255,255,0.3);*/
}

.welcome__subtitle2 {
  width: 450px;
  margin-top: -7%;
  font-size: 35px;
  font-family: 'Dongle', sans-serif;
  color: gray;
  line-height: 1.9rem;
  animation-name: welcome__title-anim;
  animation-delay: 2.5s;
  animation-fill-mode: forwards;
  opacity: 0;
  animation-duration: 1s;
}
.welcome__btn {
    width: 180px;
    height: 50px;
    background-color: black;
    border: 1px solid transparent;
    color: white;
    font-family: 'Dongle', sans-serif;
    font-size: 30px;
    border-radius: 10px;
    margin-top: 2%;
    transition: 450ms;
    animation-name: welcome__title-anim;
    animation-delay: 3s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1s;
}
.welcome__btn p {
    width: 140px;
    height: auto;
    margin-left: 7%;
    margin-top: 3px;
}
.welcome__btn .uil-message {
    display: flex;
    justify-content: center;
    font-size: 22px;
    float: right;
    margin-top: 9px;
}
.welcome__btn:hover {

    background-color: white;
    color: black;
    cursor: pointer;
    border: 2px solid black;
}
.welcome__img {
    width: 400px;
    height: 400px;
    
    margin-left: 57%;
    margin-top: -41%;
    animation-name: welcome__img-anim;
    animation-delay: 4s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1s;
}
.welcome__img img {
    width: 580px;
    height: auto;
    
}
@keyframes welcome__img-anim {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
} 
.esim {
    width: 190px;
}
.welcome__scroll {
    width: 300px;
    height: auto;
    text-decoration: none;
    color: black;
    font-family: 'Dongle', sans-serif;
    font-size: 25px;
    margin-top: 6% !important;
    transition: 450ms;
    animation-name: welcome__title-anim;
    animation-delay: 3.5s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1s;
}
.welcome__scroll i {
    font-size: 14px;
}
.welcome__scroll a {
    color: gray;
}
#scroll__icon {
  animation-name: scroll__icon-animation;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-delay: 4.5s;
  animation-iteration-count: 100;
  
}
@keyframes scroll__icon-animation {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-30px);
  }
}
.welcome__scroll:hover {
    color: gray;
    cursor: pointer;
    transform: translateY(10px); 
}
 .icons {
    width: 10px;
    height: 200px;
    margin-left: 10%;
    margin-top: 13%;
    position: absolute;
    color: #8d00f9;
    font-size: 20px;
    animation-name: welcome__title-anim;
    animation-delay: 2.5s;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-duration: 1.2s;
}
.icons p {
    line-height: 2rem;
}
.icons a {
    text-decoration: none;
}
.icons a:hover {
    color: white;
    transform: scale(1.1);
}

.langSettings {
    width: 45px;
    height: 45px;
    background-color: rgba(255, 255, 255, 0.23);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(3.2px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    position: fixed;
    left: 2%;
    top: 90%;
    transition: 450ms;
}
.langSettings i {
    color: #101010;
    font-size: 25px;
    margin-left: 1px;
    margin-top: 10px !important;
    transition: 1000ms;
    justify-content: center;
    display: flex;
    
}
.langSettings i:hover {
    transform: rotate(250deg);
    cursor: pointer;
}   
.langSettings:hover {
    transform: scale(1.1);
    box-shadow: transparent;
}
.langs {
    width: 200px;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.23);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(3.2px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 20px;
    font-size: 30px;
    line-height: 1rem;
    position: fixed;

    top: 88%;
    left: 7%;   
    display: none;
    transition: 250ms;
}
.langs i {
    font-size: 34px;
    display: flex;
    justify-content: left;
    float: left;
    margin-left: 25px;
    margin-top: 13px;
    transition: 250ms;
    opacity: 0;
}

.langs i:hover {
    cursor: pointer;
    color: gray;
}
.langs.active {
    animation-name: socialOpen;
    animation-duration: 350ms;
    animation-fill-mode: forwards;
    display: block;
    

}
@keyframes socialOpen{
    from {
        width: 0;
    }
    to {
        width: 200px;
    }
}
 .langs .icons1.iconAnim1 {
    animation-name: icons;
    animation-duration: 450ms;
    animation-fill-mode: forwards;
    animation-delay: 100ms;
} 
.langs .icons2.iconAnim2 {
    animation-name: icons;
    animation-duration: 450ms;
    animation-fill-mode: forwards;
    animation-delay: 220ms;
} 
.langs .icons3.iconAnim3 {
    animation-name: icons;
    animation-duration: 450ms;
    animation-fill-mode: forwards;
    animation-delay: 340ms;
} 
@keyframes icons {  
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }

}
.mystyle {
    position: fixed;
    border-bottom: 2px solid gray;
    top: -5px;
    background-color: white;
    height: 80px;
}
.logoChange {
    
    width: 110px !important;
    height: auto !important;
}
@media  screen and (max-width: 1400px) {
    .nav-menu {
        position: fixed;
        left: -180%;
        top: 7rem;
        flex-direction: column;
        background-color: #fff;
        width: 100%;
        border-radius: 10px;
        text-align: center;
        transition: 0.3s;
        box-shadow:
            0 10px 27px rgba(0, 0, 0, 0.05);
            z-index: 20;
    }

    .nav-menu.active {
        left: -65%;
    }

    .nav-item {
        margin: 2.5rem 0;
    }

    .hamburger {
        display: block;
        cursor: pointer;
        
    }
    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
    .welcome__img img {
        width: 130%;
        height: auto;
        margin-top: -12%;
    }
    .welcome__title {
        margin-top: 15%;
    }
}
@media screen and (min-width: 1150px) and (max-width: 1280px) {
    .welcome__img img {
        width: 120%;
        height: auto;
    }
} 
@media screen and (max-width:1150px) and (min-width: 1000px) {
    .welcome__img img {
        width: 100%;
        height: auto;
        margin-top: -120px;
    }

}
@media screen and (max-width: 1000px) and (min-width: 700px) {
    .welcome__img {
        display: none;
    }
    .home__welcome {
        width: 100%;
        text-align: center;
        margin: -11% auto;
        line-height: 8rem;
        
    }
    .welcome__subtitle2 {
        margin-left: 22%;

    }
    .welcome__btn {
        margin-left: -5%;
    }
    .welcome__scroll {
        display: none;
    }
    .welcome__title {
        margin-left: -4%;
    }
} 
@media screen and (max-width: 700px ) and (min-width: 550px) {
    .welcome__img {
        display: none;
    }
    .home__welcome {
        width: 100%;
        text-align: center;
        margin: -11% auto;
        line-height: 8rem;
        
    }
    .welcome__subtitle2 {
        margin-left: 15%;
        
    }
    .welcome__btn {
        margin-left: -5%;
    }
    .welcome__scroll {
        display: none;
    }
    .welcome__title {
        margin-left: -4%;
    }
}
@media screen and (max-width: 550px ) and (min-width: 350px) {
    .welcome__img {
        display: none;
    }
    .home__welcome {
        line-height: 3rem;

    }
    .welcome__subtitle2 {
        width: 350px;
    }
    
}













